<template>
  <div :class="map_class">
    <div id="map"></div>
  </div>
</template>

<script>
/* eslint-disable */
import echarts from 'echarts'
import map3D from 'echarts-gl'
import 'echarts/map/js/province/shandong.js'
export default {
  name: 'map3d',
  data() {
    return {
      map_class: 'map_content'
    }
  },
  created() {
    if (this.$route.path === '/workspace/index') {
      this.map_class = 'sm_map'
    } else {
      this.map_class = 'map_content'
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.chart = echarts.init(document.getElementById('map'))
      var regionData = [
        {
          name: '',
          value: '0.01'
        },
        {
          name: '',
          value: '2.01'
        },
        {
          name: '',
          value: '0.15'
        },
        {
          name: 'Andorra',
          value: ''
        },
        {
          name: '',
          value: '3.86'
        },
        {
          name: '',
          value: '5.73'
        },
        {
          name: '',
          value: '8.40'
        },
        {
          name: '',
          value: '1.48'
        },
        {
          name: '',
          value: '9.02'
        },
        {
          name: '',
          value: '11.08'
        },
        {
          name: '',
          value: '4.54'
        },
        {
          name: '',
          value: '2.10'
        },
        {
          name: '',
          value: '6.98'
        },
        {
          name: '',
          value: '0.00'
        },
        {
          name: '',
          value: ''
        },
        {
          name: '',
          value: '5.53'
        },
        {
          name: '',
          value: '10.63'
        }
      ]
      const option = {
        visualMap: {
          show: true,
          min: 0,
          max: 15,
          inRange: {
            color: [
              '#313695',
              '#4575b4',
              '#74add1',
              '#abd9e9',
              '#e0f3f8',
              '#ffffbf',
              '#fee090',
              '#fdae61',
              '#f46d43',
              '#d73027',
              '#a50026'
            ]
          }
        },
        series: [
          {
            type: 'map3D',
            map: '',
            viewControl: {
              center: [-10, 0, 10]
            },
            environment: new echarts.graphic.LinearGradient(
              0,
              0,
              0,
              1,
              [
                {
                  offset: 0,
                  color: '#00aaff' // 
                },
                {
                  offset: 0.7,
                  color: '#998866' // 
                },
                {
                  offset: 1,
                  color: '#998866' // 
                }
              ],
              false
            ),
            light: {
              main: {
                intensity: 0.8,
                shadow: true,
                alpha: 150,
                beta: 70
              },
              ambient: {
                intensity: 0
              },
              ambientCubemap: {
                diffuseIntensity: 1,
                texture:
                  'https://gallerybox.echartsjs.com/asset/get/s/data-1491896094618-H1DmP-5px.hdr'
              }
            },
            groundPlane: {
              show: true
            },
            data: regionData,
            postEffect: {
              enable: false
            },
            itemStyle: {
              borderColor: 'rgb(62,215,213)',
              borderWidth: 1
            }
          }
        ]
      }
      this.chart.setOption(option)
    }
  }
}
</script>

<style scoprd lang="scss">
.map_content {
  width: 1200px;
  margin: 0 auto;
  #map {
    width: 1200px;
    height: 800px;
    margin-top: 40px;
  }
}
.sm_map {
  width: 400px;
  margin: 0 auto;
  #map {
    width: 400px;
    height: 260px;
    margin-top: 6px;
  }
}
</style>



